@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}
$min-width: 320px;
$max-width: 1920px;

@mixin fluid-prop($prop, $min-vw, $max-vw, $min-size, $max-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-size);
  $u4: unit($max-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      #{$prop}: $min-size;
      @media screen and (min-width: $min-vw) {
        #{$prop}: calc(
          #{$min-size} + #{strip-unit($max-size - $min-size)} *
            ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})
        );
      }
      @media screen and (min-width: $max-vw) {
        #{$prop}: $max-size;
      }
    }
  }
}
.chart-container {
  width: 100%;
  padding: 50px 0 50px;
}
.recharts-responsive-container{
  min-height: 33vh;
  max-height: 33vh;
  max-width: 1200px;
  margin: auto;
}

.chart-heading {
  text-align: center;
  @include fluid-prop('font-size', $min_width, $max_width, 32px, 64px);
  font-family: 'Barriecito', 'Livvic', sans-serif;
  margin: auto auto 0;
  line-height: 1;
  font-weight: 800;
  max-width: 700px;
  @media screen and (max-width: 768px) {
    max-width: 90%;
  }
  .per {
    font-size: 66%;
  }
}
.chart-subheading {
  text-align: center;
  margin-bottom: 100px;
  @include fluid-prop('font-size', $min_width, $max_width, 24px, 36px);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
